import React, {useState} from 'react';
import {Link} from 'react-router-dom';
import { FaBars, FaCartPlus} from "react-icons/fa";
import CartSection from './CartSection';

const Header = ({product, setProduct, carts, setCarts,sum,total}) => {
    const [navBurger,setNavBurger] = useState(0);
    const [cartIcon,setCartIcon] = useState(0);

    const onCart = () =>{
        if(cartIcon === 0){
            setCartIcon(100);
        }
        else{setCartIcon(0);
        }
    }
    const onNav = () =>{
        if(navBurger === 0){
            setNavBurger(100);
        }
        else{setNavBurger(0);
        }
    }
    
    const linkStyle = {color:'white', textDecoration:"none"};

    return (
        <div className="header">
            <div className="header_main">
                <Link to="/" style={linkStyle}><h1>Potato Planet</h1></Link>
                    <div className="header_icon">
                        <ul>
                            <Link to="/about" style={linkStyle}><li>About</li></Link>
                            <Link to="/product" style={linkStyle}><li>Store</li></Link>
                            <Link to="/blog" style={linkStyle}><li>Blog</li></Link>
                            <Link to="/contact" style={linkStyle}><li>Contact</li></Link>
                        </ul>
                        <div className="header_cart" onClick={onCart}>
                            <h2><FaCartPlus /></h2>
                            <p className="header_cart_sum">{carts.length}</p>
                        </div>
                        <div className="cart_menu" >
                            <CartSection product={product} setProduct={setProduct}  carts={carts} setCarts={setCarts} 
                            total={total} cartIcon={cartIcon}/>
                        </div>
                </div>
            </div>
            <div className="header_side">
                <h2><FaBars onClick={onNav}/></h2>
                <div className="nav_menu" style={{transform: `translateX(${navBurger}%)`}}>
                        <h1>Menu</h1>
                        <ul>
                            <Link to="/about" style={linkStyle}><li>About</li></Link>
                            <Link to="/product" style={linkStyle}><li>Store</li></Link>
                            <Link to="/blog" style={linkStyle}><li>Blog</li></Link>
                            <Link to="/contact" style={linkStyle}><li>Contact</li></Link>
                        </ul>
                </div>
                <Link to="/" style={linkStyle}><h1>Potato Planet</h1></Link>
                <div className="header_cart" onClick={onCart}>
                            <h2><FaCartPlus /></h2>
                            <p className="header_cart_sum">{carts.length}</p>
                </div>
                <div className="cart_menu" >
                     <CartSection product={product} setProduct={setProduct}  carts={carts} setCarts={setCarts} 
                        cartIcon={cartIcon} total={total}/>
                </div>
            </div>
            

            
        </div>
    );}
        
                
               

export default Header;