import React from 'react';
import { FaChevronUp, FaChevronDown} from "react-icons/fa";

const Cart = ({product, setProduct, carts, setCarts, cart, id, name, price, image,Qty}) => {
        //Plus Button
    const increment = ()=>{
        setCarts(
            carts.map((care) =>{
            if (care.id === cart.id){
               care.Qty++;
               care.total=care.price*care.Qty;
            }
            return care;
    }))
    }
    //Minus Button
    const decrement = ()=>{
        setCarts(
            carts.map((care) =>{
            if (care.id === cart.id){
               care.Qty--;
               care.total=care.total*care.Qty;
               if(care.Qty<0){
                   care.Qty = 0;
               }
            }
            return care;
    }))
    }
    //Delete Button
    const addCartSign = () =>{
        setProduct(
            product.map((el) =>{
            if (el.id === cart.id){
                return {
                    ...el, onCart:false,
                };
            }
            return el;
        }))
    };

    const deleteCart = ()=>{
        setCarts(carts.filter(el => el.id !== cart.id));
        addCartSign();
    }
    //JSX
    return(
        <div className="cart_card" key={id}>
            <div>            
            <p>{name}</p>
            <p>${price}</p>
                <div className="cart_Qty">
                    <h4>{Qty}</h4>
                    <div className="cart_Qty_btn">
                        <button onClick={increment} className="Qty_btn"><FaChevronUp/></button>
                        <button onClick={decrement} className="Qty_btn"><FaChevronDown/></button>
                    </div>
                </div>
            </div>
            <button className="delete_btn" onClick={deleteCart}>X</button>
        </div>
    );
    
}

export default Cart;