import React from 'react';
import Button from './Button';


const Item = ({product, setProduct, carts, setCarts, prod, id, name, price, image, onCart, description}) => {
    //Add Button
     const onCartSign = () =>{

        setProduct(
            product.map((el) =>{
            if (el.id === prod.id){
                return {
                    ...el, onCart:true,
                };
            }
            return el;
        }))
    };
    
    const addCart = (e) =>{
        
        const onlyOne = carts.map(a => a.id);
        
        if(
            !onlyOne.includes(prod.id)
        ){  
            setCarts([
                ...carts, {id:prod.id, name: prod.name, price:prod.price, image:prod.image, Qty:1, total: prod.price*1},
            ]);
            onCartSign();
        }

        
        
    };    
    
    return(
            <div className="product_card" key={id}>
                <div className="imgWrap">
                    <img src={image} alt={name}/>
                </div>
                <div className="product_content">
                    <h1>{name}</h1>
                    <p>{description}</p>
                    <div class="price">
                    <div>
                    <h4>Price:</h4>
                    <h2>${price}</h2>
                    </div>
                    <Button addCart={addCart} 
                    text={onCart ? 'on Cart' : 'add Cart'} color={onCart ? 'red' : 'green'}/>
                    </div>
                </div>
            </div>
    );
}

//<button onClick={totalPrice}>Peace</button>
export default Item;