import React from 'react';
import {Link} from 'react-router-dom';

const Banner = ()=>{
    return(
        <div className="banner">
            <content>
            <h2>Welcome to </h2>
            <h1>Potato Planet</h1>
            <p>In et veniam non incididunt nisi non dolor magna.</p>
            <Link to="/product"><button>See More</button></Link>
            </content>
            <img src='/images/potato.png' alt={"potato"}/>
            
        </div>
    );
}

export default Banner;
