import React from 'react';
import BlogHome from './BlogHome';

const Section = ({blogs, setBlogs})=>{
    
    return(
        <>
        <div className="product_title">
        <div className="title_line"></div>
        <h1>Potato Blog</h1>
        <div className="title_line"></div>
        </div>
        <div className="section">
        {blogs.map(blog=>(
                <BlogHome image={blog.image} blog={blog} blogs={blogs} setBlogs={setBlogs} title={blog.title} content={blog.content} id={blog.id}/>
            ))}    
        </div>
        </>
    );
}

export default Section;
