import React from 'react';

const Contact = ()=>{
    return(
        <div className="contact_container">
        <div className="product_title">
        <div className="title_line"></div>
        <h1>Contact Us</h1>
        <div className="title_line"></div>
        </div>
        <div className="contact">
            <div>
                <p>Your Name</p>
                <input type="text" />
            </div> 
            <div>
                <p>Your Email</p>
                <input type="text" />
            </div> 
            <div className="mssg">
                <p>Your Message</p>
                <input type="text" />
            </div>
            <input type="submit" value="Send Message"/> 
        </div>
        </div>
    );
}

export default Contact;
