import React from 'react';
import Carts from './Carts';

const CartSection = ({product, setProduct, carts, setCarts,total,cartIcon}) => {
    //SUM
    
   //style={{transform: `translateY(-${navBurger}%)`, opacity: cartDisplay ? '0.7' : '1' }} 
    return(
        <div className="cart_section"  
        style={{transform: `translateX(-${cartIcon}%)`}}
        >
            <h1>Cart</h1>
            <h3>Total Item: ${total}</h3>
            <Carts carts={carts} setCarts={setCarts} setProduct={setProduct} product={product}/>
        </div>
    );
}

export default CartSection;