import React from 'react';
import Blog from './Blog';

const Blogs = ({blogs, setBlogs})=>{
    return(
        <>
        <div className="product_title">
        <div className="title_line"></div>
        <h1>Potato Blog</h1>
        <div className="title_line"></div>
        </div>
        <div className="blog">
            {blogs.map(blog=>(
                <Blog blog={blog} blogs={blogs} setBlogs={setBlogs} image={blog.image} title={blog.title} content={blog.content} id={blog.id}/>
            ))}
        </div>
        </>
    );
}

export default Blogs;