import React from 'react';
import StoreItem from './StoreItem';


const Store = ({product, setProduct, carts, setCarts}) =>{
    const productClone = product.slice(0);
    const bestProduct =  productClone.splice(4, productClone.length - 4);
    

    return(
        <div className="store">

        <div className="store_title">
            <div className="title_line"></div>
            <h1>Best Seller</h1>
            <div className="title_line"></div>
        </div>
        <div className="store_section">
        { productClone.map(prod=>(
    <StoreItem product={product} setProduct={setProduct}  bestProduct={bestProduct} carts={carts} setCarts={setCarts} prod={prod}
        id={prod.id} name={prod.name} image={prod.image} price={prod.price} onCart={prod.onCart}/>
))}
        </div>

        </div>
    );
}

export default Store;

