import React from 'react';
import Cart from './Cart';

const Carts = ({product, setProduct, carts, setCarts}) => {
        return(
        <div className="cart_main">
        { carts.map(cart=>(
            <Cart product={product} setProduct={setProduct}  carts={carts} setCarts={setCarts} cart={cart}
                    id={cart.id} name={cart.name} price={cart.price} image={cart.image} Qty={cart.Qty}/>
        ))}
        </div>
    );
}

export default Carts;