import React from 'react';
import Item from './Item';

const Items = ({product, setProduct, carts, setCarts}) => {  
    
    return(
        <>
        <div className="product_title">
        <div className="title_line"></div>
        <h1>Our Product</h1>
        <div className="title_line"></div>
        </div>
        <div className="product_section">
        { product.map(prod=>(
            <Item product={product} setProduct={setProduct}  carts={carts} setCarts={setCarts} prod={prod}
                    id={prod.id} name={prod.name} description={prod.description} image={prod.image} price={prod.price} onCart={prod.onCart}/>
        ))}
        </div>
        </>
    );
}

export default Items;